.main {
    margin: 5px;
}

.main__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;    
    gap: 5px;
}

.main__card-first {
    display: flex;
            flex-direction: column;
            align-items: center;
            padding: 70px 20px 30px;
            background-color: var(--bg-color-container);
            border-radius: 20px;
            max-width: 320px;
}

.main__card-second {
    background-image: url('../img/hero-mob-2x.jpg');
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
            border-radius: 20px;
            height: 390px;
            width: 320px;
                
            position: relative;
}
