.main__title {
    font-family: 'TT Firs Neue';
    font-weight: 500;
    font-size: 40px;
    line-height: 120%;
    text-transform: uppercase;
    color: var(--primary-color);

    margin-bottom: 20px;
    width: 270px;

//     // @include desktop {
//     //     font-size: 80px;
//     //     margin-bottom: 32px;
//     //     }
}

.main__text {
    width: 270px;
    font-weight: 500;
    font-size: 16px;
    line-height: 130%;
    color: var(--primary-color);

    margin-bottom: 100px;

    // @include desktop {
    //     width: 528px;
    //     font-size: 24px;
    //     margin-bottom: 419px;
    // }
}

.orange__btn {
    width: 280px;
    height: 41px;
    font-family: 'Manrope';
    font-weight: 600;
    font-size: 14px;
    line-height: 150%;
    color: var(--primary-color);
    background: var(--first-accent-color);
    border-radius: 110px;
    border: none;

    cursor: pointer;

    padding: 10px 73px;

    // @include desktop {
    //         font-size: 24px;
    //         width: 100%;
    //         height: 91px;
    //     }
}

.main__img {
    width: 130px;
    position: absolute;
    top: 31px;
    left: 20px;
    transform: rotate(-4.38deg);

//     // @include desktop {
//         //     width: 240px;
//         //     top: 110px;
//         //     left: 100px;
//         // }    
}

.text-block {
    margin-bottom: 5px;
}

.text-block__container {
    padding: 29px 0;
    text-align: center;
}

.text-block__text {
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
    color: var(--primary-color);
}

.info {
    flex-wrap: wrap;
        justify-content: center;
        gap: 5px;
        display: flex;
}

.info__container {
    padding: 30px 20px;
}

// .info__container:last-child {
//     margin-bottom: 5px;
// }

.info__title {
    font-family: 'TT Firs Neue';
    font-weight: 500;
    font-size: 30px;
    line-height: 108%;
    text-transform: uppercase;
    color: var(--primary-color);

    margin-bottom: 30px;
}

.info__list {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.info__item,
.info__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 160%;
    color: var(--primary-color);
}

