.title-block {
    margin: 5px 0;
}

.title-block__container {
    padding: 30px 20px;
}

.title-block__title {
    font-family: 'TT Firs Neue';
    font-weight: 500;
    font-size: 31px;
    line-height: 108%;
    text-transform: uppercase;
    color: var(--primary-color);
}

.title-block__container--purple {
    background-color: var(--second-accent-color);
}

.examples__list {
    flex-wrap: wrap;
        justify-content: center;
        gap: 5px;
        display: flex;
}
