body {
    margin: 0;
    background-color: black;
    font-family: 'Manrope';
}


h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
}

ul,
ol {
    margin: 0;
    padding: 0;
    list-style: none;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
    margin: 0;
}

button {
    cursor: pointer;
}